<template>
    <div class="create-link" v-if="$route.name == 'createLink'">
        <link-common @submitLinkForm="createNewLink"></link-common>
    </div>
</template>

<script>
import linkCommon from './linkCommon/linkCommon';
export default {
    name: 'createLink',
    components: {
        linkCommon
    },
    data() {
        return {
            prevLinkForm: {}
        }
    },
    methods: {
        createNewLink(linkForm) {
            this.$axios.post('/link/create', linkForm)
            .then(res => {
                this.$Message.info('链接创建成功')
                window.location.href = '/statistics'
            })
        }
    }
}
</script>



