<template>
    <div v-if="$route.name == 'dataStatistics'">
        <Button class="back-home" type="warning" size="large" to="/statistics">返回首页</Button>
        <Table class="data-table" :columns="dataHeader" :data="dataList" :loading="isLoading"></Table>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isLoading: true,
            dataList: [],
            dataHeader: [
                {
                    key: 'linktime',
                    title: '创建日期',
                    width: 100,
                    align: 'center'
                }, {
                    key: 'qudao',
                    title: '渠道名称',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'name',
                    title: '小说平台&&服务号名称',
                    // width: 90,
                    align: 'center'
                }, {
                    key: 'uv',
                    title: '百度uv',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'yuedu',
                    title: '累计阅读人数',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'count_pay',
                    title: '付款数量',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'pay_rate',
                    title: '付款率',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'today_money',
                    title: '今日充值金额',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'money',
                    title: '累计充值金额',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'today_cost',
                    title: '今日成本',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'cost',
                    title: '总成本',
                    width: 90,
                    align: 'center'
                }, {
                    key: 'today_back',
                    title: '动态回本率',
                    // width: 90,
                    align: 'center'
                }, {
                    key: 'back',
                    title: '回本率',
                    width: 90,
                    align: 'center'
                }
            ]
        }
    },
    mounted() {
        this.showDataList()
    },
    methods: {
        showDataList() {
            let url = sessionStorage.getItem("url");
            if(!url) {
                url = this.$route.params.url
                sessionStorage.setItem("url", this.$route.params.url);
            }
            this.$axios.get('/tongji', {params: {url: url}})
            .then(res => {
                this.isLoading = false
                console.log(res.data.data)
                this.dataList.push(res.data.data)
                console.log(this.dataList)
            })
        }
    },
    destroyed() {
        sessionStorage.setItem("url", '');
    }
}
</script>

<style scoped>
.back-home {
    float: left;
    margin: 30px 0;
}

.data-table {
    clear: both;
}
</style>



