import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/views/Index'
import createLink from '@/components/indexs/createLink'
import updateLink from '@/components/indexs/updateLink'
import dataStatistics from '@/components/indexs/dataStatistics'

import Account from '@/views/Account'
import createAccount from '@/components/accounts/createAccount'

import Recharge from '@/views/Recharge'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/statistics',
            redirect: '/statistics/index'
        },
        {
            path: '/statistics/index',
            name: 'Index',
            component: Index,
            children: [
                {
                    path: 'createLink',
                    name: 'createLink',
                    component: createLink
                }, {
                    path: 'updateLink',
                    name: 'updateLink',
                    component: updateLink
                }, {
                    path: 'dataStatistics',
                    name: 'dataStatistics',
                    component: dataStatistics
                }
            ]
        },
        {
            path: '/statistics/account',
            name: 'Account',
            component: Account,
            children: [
                {
                    path: 'createAccount',
                    name: 'createAccount',
                    component: createAccount,
                }
            ]
        },
        {
            path: '/statistics/recharge',
            name: 'Recharge',
            component: Recharge,
        }
    ]
})



