<template>
    <div class="account">
        <div class="account-page" v-if="$route.name == 'Account'">
            <Button class="create-btn" size="large" type="success" shape="circle" to="/statistics/account/createAccount">
                添加新账号
                <Icon type="ios-arrow-forward" />
            </Button>
            <Table class="account-table" stripe :columns="accountHeader" :data="accountData"></Table>
        </div>
        <router-view/>
    </div>
</template>

<script>
export default {
    data() {
        return {
            accountData: [],
            accountHeader: [
                {
                    'key': 'name',
                    'title': '小说平台&&服务号名称',
                    'align': 'center'
                },
                {
                    'key': 'username',
                    'title': '账号',
                    'align': 'center'
                },
                {
                    'key': 'password',
                    'title': '密码',
                    'align': 'center'
                },
                {
                    'title': 'Action',
                    'align': 'center',
                    render: (h, params) => {
                        let that = this;
                        return h('div', [
                            h('Button', {
                                style: {
                                    'marginRight': '10px'
                                },
                                props: {
                                    type: 'warning',
                                    size: 'small'
                                },
                                on: {
                                    click() {
                                        that.$axios.get('/user/del', {params: {id: params.row._id}})
                                        .then(res => {
                                            that.$Message.info('删除成功')
                                            that.accountData.splice(params.index, 1)
                                        })
                                    }
                                }
                            }, '删除')
                        ])
                    }
                }
            ]
        }
    },
    mounted() {
        this.showAccountList()
    },
    methods: {
        showAccountList() {
            this.$axios.get('/user')
            .then(res => {
                this.accountData = res.data.data
            })
        }
    }
}
</script>


<style scoped>
.create-btn {
    margin: 30px;
}
</style>



