<template>
    <div id="app">
        <Menu mode="horizontal" theme="light" :active-name="onRoutes" @on-select="selectMenu">
            <MenuItem name="/statistics/index">
                <Icon type="ios-paper" />
                平台数据
            </MenuItem>
            <MenuItem name="/statistics/account">
                <Icon type="ios-people" />
                账号管理
            </MenuItem>
            <MenuItem name="/statistics/recharge">
                <Icon type="ios-people" />
                充值统计
            </MenuItem>
        </Menu>
        <transition name="move" mode="out-in">
            <keep-alive>
                <router-view />
            </keep-alive>
        </transition>
    </div>
</template>

<script>
export default {
    name: 'App',
    data() {
        return {
            onRoutes: this.$route.path
        }
    },
    methods: {
        selectMenu(name) {
            this.$router.replace({path: name})
        }
    }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding: 0 30px;
}
* {
    margin: 0;
    padding: 0;
}
a {
    text-decoration: none;
}

ul, ol {
    list-style: none;
}

input {
    outline: none;
}

img {
    display: block;
    border: 0;
}
</style>



