<template>
    <div class="account-common">
        <Form class="form-cont" :model="accountForm" :label-width="150">
            <FormItem label="小说平台&&服务号名称">
                <Input v-model="accountForm.name" placeholder="请输入小说平台&&服务号名称"></Input>
            </FormItem>
            <FormItem label="账号">
                <Input v-model="accountForm.username" placeholder="请输入账号"></Input>
            </FormItem>
            <FormItem label="密码">
                <Input v-model="accountForm.password" placeholder="请输入密码"></Input>
            </FormItem>
            <div class="btn-cont">
                <Button type="success" @click="submitAccountForm">提交</Button>
                <Button type="warning" @click="cancel">取消</Button>
            </div>
        </Form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            accountForm: {}
        }
    },
    methods: {
        submitAccountForm() {
            this.$emit('submitAccountForm', this.accountForm)
            this.accountForm = {}
        },
        cancel() {
            this.accountForm = {}
            this.$router.push('/statistics/account')
        }
    }
}
</script>

<style scoped>
.form-cont {
    width: 460px;
    margin: 30px auto;
    text-align: left;
}
.btn-cont {
    text-align: center;
}
.btn-cont button {
    margin-right: 20px;
}
</style>



