<template>
    <div class="recharge">
        <div class="recharge-page" v-if="$route.name == 'Recharge'">
            <Table class="recharge-table" stripe :columns="rechargeHeader" :data="rechargeData"></Table>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            rechargeData: [],
            rechargeHeader: [
                {
                    'key': 'name',
                    'title': '小说平台&&服务号名称',
                    'align': 'center'
                },
                {
                    'key': 'createdate',
                    'title': '创建时间',
                    'align': 'center'
                },
                {
                    'key': 'money',
                    'title': '总充值',
                    'align': 'center'
                },
                {
                    'key': 'orders',
                    'title': '订单数',
                    'align': 'center'
                },
                {
                    'key': 'pay_orders',
                    'title': '支付数',
                    'align': 'center'
                },
                {
                    'key': 'pay_rate',
                    'title': '支付率',
                    'align': 'center'
                }
            ]
        }
    },
    mounted() {
        this.showRechargeList()
    },
    methods: {
        showRechargeList() {
            this.$axios.get('/order')
            .then(res => {
                this.rechargeData = res.data.data
            })
        }
    }
}
</script>

<style scoped>
.recharge-page {
    margin-top: 30px;
}
</style>




