<template>
    <div class="create-account" v-if="$route.name == 'createAccount'">
        <account-common @submitAccountForm="createNewAccount"></account-common>
    </div>
</template>

<script>
import accountCommon from './accountCommon/accountCommon';
export default {
    name: 'createAccount',
    components: {
        accountCommon
    },
    methods: {
        createNewAccount(accountForm) {
            this.$axios.post('/user/create', accountForm)
            .then(res => {
                this.$Message.info('添加账号成功')
                window.location.href = '/statistics/account'
            })
        }
    }
}
</script>



